@import 'colors';

html,
body {
    padding: 0;
    margin: 0;
    background-image: url(https://www.fractalcamo.com/uploads/5/9/0/2/5902948/s189772745713394276_p2734_i240_w1500.jpeg);
    background-image: linear-gradient(to bottom, rgba(0,0,0,0.85) 0%,rgba(0,0,0,0.85) 100%), url(https://www.fractalcamo.com/uploads/5/9/0/2/5902948/s189772745713394276_p2734_i240_w1500.jpeg);
    font-family: 'Oswald', sans-serif;
}

#header {
  width: 350px;
  padding-bottom: 20px;
  padding-top: 20px;
  box-sizing: border-box;
}

.bars {
  fill: $color-primary-2;
  opacity: 0.8;
}

#stat {
  color: #ffffff;

}

form {
  padding-left: 40px;
  padding-bottom: 10px;
}

#warm {
  padding-left: 60px;
  width: 50%;
  height: auto;
}

#head img {
  width: 275px;
  height: auto;
}

#head {
  position:absolute;
  top:5px;
  left: 400px;
}

#bio {
  position: absolute;
  right: 20px;
  top: 20px;
  color: #ffffff;
  float: right;
}

th, td {
    padding: 3px;
    text-align: left;
}

table, th, td {
    border: 1px solid white;
    border-spacing: 0 1px;
}
.svg-container {
    display: inline-block;
    position: relative;
    width: 100%;
    padding-bottom: 100%; /* aspect ratio */
    vertical-align: top;
    overflow: hidden;
}
.svg-content-responsive {
    margin-left: 60px;
    display: inline-block;
    position: absolute;
    top: 10px;
    left: 0;
}

div.tooltip { 
    position: absolute;     
    text-align: center;     
    width: 60px;          
    height: 28px;         
    padding: 2px;       
    font: 12px sans-serif;    
    background: lightgreen; 
    border: 0px;    
    border-radius: 8px;     
    pointer-events: none;     
}

.axis line,
.axis path {
  fill: none;
  stroke: #000;
  shape-rendering: crispEdges;
}

#viz {
  position: fixed;
  left: 0px;
  right: 0px;
  top: 180px;
  bottom: 0px;
}


.typeahead {
  margin-left: 40px;
  color: white;
}


.tt-query, /* UPDATE: newer versions use tt-input instead of tt-query */
.tt-hint {
    width: 396px;
    height: 30px;
    padding: 8px 12px;
    font-size: 24px;
    line-height: 30px;
    border: 2px solid #ccc;
    border-radius: 8px;
    outline: none;
}

.typeahead.tt-input {
  background-color: white !important;
  color: black;
}   

.tt-input { /* UPDATE: newer versions use tt-input instead of tt-query */
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
}

.tt-hint {
    color: #999;
    visibility: hidden;
}


g.axes line{
  stroke: white;
}

g.axes path{
  stroke: white;
}

g.axes text{
  fill: white;
}

h1 {
  color: white;
  padding-left: 40px;
  margin-top: 0px;
  margin-bottom: 0px;
}

.tt-menu { /* UPDATE: newer versions use tt-menu instead of tt-dropdown-menu */
    width: 422px;
    margin-top: 12px;
    padding: 8px 0;
    background-color: #fff;
    border: 1px solid #ccc;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    box-shadow: 0 5px 10px rgba(0,0,0,.2);
}

.tt-suggestion {
    padding: 3px 20px;
    font-size: 18px;
    line-height: 24px;
}

.tt-suggestion.tt-is-under-cursor { /* UPDATE: newer versions use .tt-suggestion.tt-cursor */
    color: #fff;
    background-color: #0097cf;

}

.tt-suggestion p {
    margin: 0;
}



